import { createStore } from "vuex";
import ax from "axios";
import moment from "moment";

let apiUrl = "https://api.metabusgo.xyz/api";
let uploadUrl = "https://api.metabusgo.xyz/upload";
if (process.env.NODE_ENV != "production") {
  apiUrl = "http://localhost/api";
  uploadUrl = "http://localhost/upload";
}

// 사용해야하는 url 이 많아져서 baseURL 이용하면 안되겠음
// axios.defaults.baseURL;

// csrf 문제 해결할려고 넣었는데 안되는거 같음
// axios.defaults.xsrfCookieName = "csrftoken";
// axios.defaults.xsrfHeaderName = "X-CSRFToken";
// axios.defaults.headers.common["X-CSRF-TOKEN"] = "hellow-localhost";

function call(reqData, resolve) {
  const axios = ax.create();
  axios
    .post(apiUrl, reqData)
    .then((res) => {
      if (res.data.code === undefined) {
        resolve({
          code: -1,
          message: "no data",
        });
        return;
      }

      resolve(res.data);
    })
    .catch((err) => {
      resolve({
        code: -1,
        message: `catch:${err.message}`,
      });
    });
}

async function callAsync(reqData) {
  // console.log(api_call, reqData);
  return new Promise((resolve) => {
    call(reqData, resolve);
  });
}

async function wait(mileSec) {
  return new Promise((resolve) => {
    setTimeout(function () {
      resolve();
    }, mileSec);
  });
}

let store = createStore({
  state: {
    user: {
      uid: 0,
      id: "",
      name: "",
    },
    userName: "guest",
    listPicture: null,
    waitingLogin: false,
  },
  actions: {},
  modules: {},
  getters: {
    isWatingLogin: function (state) {
      return state.waitingLogin;
    },
    isLogin: function (state) {
      return state.user.uid != 0;
    },
    userId: function (state) {
      return state.user.id;
    },
    userName: function (state) {
      return state.userName;
    },
    userUid: function (state) {
      return state.user.uid;
    },
  },
  mutations: {
    setUser: (state, payload) => {
      state.user.uid = payload.uid;
      state.user.id = payload.id;
      state.user.name = payload.name;
      state.userName = state.user.name;
      state.listPicture = null;
    },
    setUserName: (state, newName) => {
      state.user.name = newName;
      state.userName = newName;
    },
    clear: function (state) {
      state.user.uid = 0;
      state.user.id = "";
      state.user.name = "";
      state.userName = "";
    },
    setListPicture: function (state, rows) {
      state.listPicture = [];
      for (let i = 0; i < rows.length; i++) {
        const row = rows[i];
        state.listPicture.push({
          rid: row.rid,
          pic_name: row.pic_name,
          pic_url: row.pic_url,
          last_edit: moment().add(-row.dt_edit, "seconds"),
        });
      }
    },
    updatePicture: function (state, row) {
      if (state.listPicture == null) {
        console.warn("updatePicture skip listPicture is null", row);
        return;
      }

      for (let i = 0; i < state.listPicture.length; i++) {
        const data = state.listPicture[i];
        if (data.rid !== row.rid) {
          continue;
        }
        if (row.pic_name !== undefined) {
          data.pic_name = row.pic_name;
        }
        if (row.pic_url !== undefined) {
          data.pic_url = row.pic_url;
        }
        if (row.dt_edit !== undefined) {
          data.last_edit = moment().add(-row.dt_edit, "seconds");
        }
        state.listPicture[i] = data;
        return;
      }
      
      console.error("setPicture fail, not found", row, state.listPicture);
    },
    addPicture: function (state, payload) {
      state.listPicture.push(payload);
    },
    delPicture: function (state, picUid) {
      for (let i = 0; i < state.listPicture.length; i++) {
        if (state.listPicture[i].rid !== picUid) {
          continue;
        }
        state.listPicture.splice(i, 1);
        return;
      }
      console.error("delPicture fail, not found", picUid, state.listPicture);
    },
  },
});

store.callAsync = callAsync;
store.call = call;
store.wait = wait;
store.getLoginToken = () => {
  const loginToken = localStorage.getItem("loginToken");
  return loginToken;
};

store.getApiUrl = () => {
  //  axios.defaults.baseURL + "/api";
  return apiUrl;
};

store.uploadAsync = async (/* FormData */ fd) => {
  return new Promise((resolve) => {
    const axios = ax.create();
    axios
      .post(uploadUrl, fd)
      .then((res) => {
        // console.log("uploadAsync ok", res);
        resolve(res.data);
      })
      .catch((error) => {
        // console.error("uploadAsync ng", error.message);
        resolve({ code: -1, message: error.message });
      });
  });
};

store.getGUID = () => {
  return Math.random().toString(36).substr(2, 9);
};

store.makePicUrl = (dbUrl) => {
  // let ret = "images/hero-eye-off.svg";
  // let ret = "";
  let ret = "images/public_null_x2.png";
  if (dbUrl.length > 1) {
    const b = dbUrl.lastIndexOf(".");
    if (b > 0) {
      ret = `${dbUrl.substr(0, b)}_x2${dbUrl.substring(b)}`;
    }
  }
  console.log("url", ret);
  return ret;
};

store.getAgo = (date) => {
  const now = moment();
  const d = now.diff(date);
  const h = moment.duration(d).humanize();
  // console.log("getAgo", date.toString());
  return h;
};

export default store;
