<template>
  <div class="flex justify-between">
    <!-- 좌측 정렬-->
    <div class="flex items-center">
      <router-link class="px-3" to="/">
        <img
          class="w-8 h-8 rounded-full"
          alt="logo"
          src="/images/logo-sm.png"
        />
      </router-link>

      <div class="px-3">
        <router-link to="/about">소개</router-link>
      </div>

      <div class="px-3">
        <router-link to="/tile">타일</router-link>
      </div>

      <div class="px-3">
        <router-link to="/wall">벽</router-link>
      </div>

      <div class="px-6">
        <router-link to="/invest">투자</router-link>
      </div>
    </div>
    <!-- 우측 정렬-->
    <div class="flex items-center" v-if="!waitResponse">
      <div class="mr-3">
        <router-link
          v-if="!$store.getters.isLogin"
          to="/login"
          class="tw-indigo-btn"
          >로그인</router-link
        >
        <router-link v-else to="/profile" class="tw-indigo-btn">{{
          $store.getters.userName
        }}</router-link>
      </div>
      <div>
        <router-link
          v-if="!$store.getters.isLogin"
          to="/signup"
          class="tw-yello-btn"
          >가입</router-link
        >
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      waitResponse: false,
    };
  },
  async mounted() {
    // console.log("Header mounted");

    const loginToken = localStorage.getItem("loginToken");
    if (loginToken === null) {
      return;
    }

    const reqData = {
      api: "loginWithToken",
      loginToken: loginToken,
    };
    // console.log("Header token exists", reqData);

    this.$store.state.waitingLogin = true;
    this.waitResponse = true;
    const res = await this.$store.callAsync(reqData);
    this.waitResponse = false;
    this.$store.state.waitingLogin = false;

    if (res.code != 100) {
      // console.log("Header loginWithToken ng", res.message);
      if (res.code == -2) {
        localStorage.setItem("loginToken", null);
        return;
      }
      return;
    }

    // console.log("Header loginWithToken ok", res);
    this.$store.commit("setUser", res.user);
  },
};
</script>
